<template>
  <div class="appDetail">
    <p class="appDetail_p">小程序详情</p>
      <div class="content">
        <section class="header">
          <img :src="'https://weapp.xiguawenhua.com/' + pic" class="app_img" />
          <span>{{name}}</span>
          <p>
            <span>类型</span>
            <span>{{type}}</span>
          </p>
          <p>
            <span>状态</span>
            <span>{{is_publish}}</span>
          </p>
          <p>
            <span>启用</span>
            <span>{{is_forbidden}}</span>
          </p>

        </section>
        <section class="app_info">
          <p><span class="space">价格</span><span style="margin-left: 0">{{fee}}</span></p>
          <p><span class="space">简介</span><span style="max-width: 400px;margin-left: 0">{{desc}}</span></p>
          <p><span>创建时间</span><span>{{create_time}}</span></p>
          <p><span>到期时间</span><span>{{user_time}}</span></p>
          <p><span>网站网址</span><span>{{site_url}}</span></p>
          <p><span>具体地址</span><span>{{address}}</span></p>
          <p v-show="show"><span>营业时间</span><span>{{start_time}} - {{over_time}}</span></p>
          <p v-show="show"><span>周末是否营业</span><span>{{business}}</span></p>
        </section>
        <section class="person_info">
          <p style="font-size: 18px">所属人信息</p>
          <p><span class="space">姓名</span><span style="margin-left: 0">{{username}}</span></p>
          <p><span class="tel">手机号</span><span style="margin-left: 20px">{{tel}}</span></p>
          <p><span>联系电话</span><span>{{notifytel}}</span></p>
          <p><span class="space">邮箱</span><span style="margin-left: 0">{{notifyemail}}</span></p>
        </section>
        <!--预约小程序-->
        <!--<section class="book" v-show="show">-->
      <!---->
        <!--</section>-->
      </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        show:false,
        is_forbidden:'',
        username:'',
        name:'',
        create_time:'',
        fee:'',
        tel:'',
        desc:'',
        address:'',
        is_publish:'',
        start_time:'',
        over_time:'',
        pic:'',
        user_time:'',
        notifytel:'',
        notifyemail:'',
        business:'',
        site_url:'',
        type:'',

      }
    },
    methods: {
      getUserDetail() {
        this.post("system/Info/getUserAppById", {
          app_id: this.$route.params.id
        }).then(res => {
          console.log(res.data)
          if(res.data.code === 10000) {
            if(res.data.data.is_forbidden === 0){
              this.is_forbidden = "启用"
            }else{
              this.is_forbidden = "禁用"
            }
            if(res.data.data.business === 1){
              this.business = "不营业"
            }else{
              this.business = "营业"
            }
            if(res.data.data.type === "预约小程序"){
              this.show = true
            }else if(res.data.data.type === "电商小程序"){
              this.show = false
            }
            this.type = res.data.data.type;
            this.username = res.data.data.username;
            this.name = res.data.data.name;
            let time = new Date (res.data.data.create_time *1000 );
            this.create_time =  time.getFullYear()+'/'+ (time.getMonth()-0+1)+'/'+time.getDate() + ' ' + time.getHours()+':'+time.getMinutes();
            let user_time = new Date (res.data.data.use_time *1000 );
            this.user_time = user_time.getFullYear()+'/'+ (user_time.getMonth()-0+1)+'/'+user_time.getDate() + ' ' + user_time.getHours()+':'+user_time.getMinutes();
            this.fee = res.data.data.fee;
            this.tel = res.data.data.tel;
            this.desc = res.data.data.desc;
            this.address = res.data.data.address;
            this.notifytel = res.data.data.notifytel;
            this.notifyemail = res.data.data.notifyemail;
            this.site_url = res.data.data.site_url;
            this.pic = res.data.data.pic;
            this.start_time = res.data.data.start_time;
            this.over_time = res.data.data.over_time;
            if(res.data.data.is_forbidden === 0){
              this.is_publish = "未发布"
            }else if(res.data.data.is_forbidden === 1){
              this.is_publish = "已绑定"
            }else if(res.data.data.is_forbidden === 2){
              this.is_publish = "已上传代码"
            }else if(res.data.data.is_forbidden === 3){
              this.is_publish = "审核中"
            }else if(res.data.data.is_forbidden === 4){
              this.is_publish = "审核中"
            }


          } else {
            this.data = "暂无数据"
            const content = res.data.msg
            this.$Modal.error({
              title: "获取失败",
              content: content
            });
          }
        });
      },

    },
    created() {
      this.getUserDetail()
    },
  }
</script>

<style scoped>
  .appDetail_p{font-size: 26px}
  .content{padding: 10px 20px}
  .content p{
    padding: 8px 0;
  }
  .content p>span:first-child{
    color: #999999;
    position: relative;
  }
  .content p>span:nth-child(2){
    display: inline-block;
    margin-left: 30px;
  }
  .app_img{width: 100px;height: 100px;border-radius: 50%}
  .header{display: flex;align-items: center}
  .header>p{
    padding: 0 2em;
  }
  .header>span{
    display: inline-block;
    padding: 0 2em;
  }
  .header>p>span:nth-child(1){
    color: #999999;
    position: relative;
  }
  .header>p>span:nth-child(1):after{
    content: ':';
    position: absolute;
    left: 3.5em;
  }
    /*小程序信息*/
  .app_info {
      padding: 20px 0;
  }
  .app_info>p>span:nth-child(1):after{
    content: ':';
    color: black;
    position: absolute;
    left: 4.5em;
  }
  .space{
    letter-spacing: 2em;
  }
  .tel{
    letter-spacing: 0.5em;
  }
  .person_info{
    padding: 30px 0;
  }
  .person_info>p>span:nth-child(1):after{
    content: ':';
    color: black;
    position: absolute;
    left: 4.5em;
  }
  .time{
    letter-spacing: 0.6em;
  }
  .book>p>span:nth-child(1):after{
    content: ':';
    color: black;
    position: absolute;
    left: 6.5em;
  }
</style>



